.verification__status {
	margin: 75px 0 0 0;
	text-align: center;
}

.verification__title {
	color: #fff;
	font-size: 13px;
	opacity: .7;
}

.status {
	font-size: 71px;
	text-transform: uppercase;
	background: linear-gradient(to right, #01ECA0 0%, #01DDB2 100%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    opacity: .5;
    @include phones {
    	font-size: 50px;
    }
    &--progress {
    	background: linear-gradient(to right, #DA1845 0%, #865285 100%);
    	background-clip: text;
       -webkit-text-fill-color: transparent;
        opacity: .8;
        @include phones {
        	font-size: 40px;
        }
    }
}

.btn--verif {
	background-color: rgba(255, 255, 255, .3);
	width: 35%;
	margin: 0 auto;
	padding: 25px 0;
	@include tablets {
		width: 50%;
	}
	@include phones {
		width: 100%;
		padding: 15px 0;
	}
}